.App {
  color:#333;
  border: 1px solid #aaa;
  border-radius: 4px;
  background-color: white;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
  border: 2px red;
}

.App > .inputWrapper > .todoList> li:nth-child(odd){
  background: red;
  border: 1px solid;
}

.App > .inputWrapper > .todoList > li:nth-child(even) {
   background: transparent;
}
